let appStarted = false
const startApp = () => {
	if (appStarted) return
	appStarted = true
	import('@/app.js')
}

if ('serviceWorker' in navigator) {
	navigator.serviceWorker.register(new URL('./sw/index.js', import.meta.url)).then(async r => {
		if (r.installing) {
			console.log('installation of new service worker...')
			r.installing.addEventListener('statechange', ev => {
				console.log(ev.currentTarget.state)
				if (ev.currentTarget.state === 'activated') startApp()
			})
		} else if (r.active) {
			// const manifest = await fetch('/manifest.json', { cache: 'no-store' }).then(s => s.json())
			// const currentManifest = await fetch('/manifest.json')
			startApp()
		}
	}).catch(err => {
		console.error(err)
		startApp()
	})
} else {
	startApp()
}